import { MenuItem } from 'components/atoms';
import { motion } from 'framer-motion';
import React from 'react';
import { AnimatedLink } from '../animated-link/animated-link';
import { Dropdown } from '../dropdown/dropdown';

interface MenuDropdownProps {
  menu: JSX.Element[],
}

const MenuDropdown = ({ menu }: MenuDropdownProps) => (
  <Dropdown
    trigger={(
      <MenuItem><AnimatedLink>dropdown</AnimatedLink></MenuItem>
    )}
    menu={menu}
  />
);

interface MenuDropdownItemProps {
  children: React.ReactNode;
}

const MenuDropdownItem = ({ children }: MenuDropdownItemProps) => (
  <motion.div
    initial={{ color: 'grey' }}
    whileHover={{ color: 'white' }}
    className="cursor-pointer block py-2 px-4"
  >
    {children}
  </motion.div>
);

export { MenuDropdown, MenuDropdownItem };
