/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { AnimatePresence, motion } from 'framer-motion';
import React, { useCallback, useState } from 'react';
import { useOutsideClick } from 'utils';

interface DropdownProps {
  trigger: JSX.Element,
  menu: JSX.Element[],
}

const Dropdown = ({ trigger, menu }: DropdownProps) => {
  const [open, setOpen] = useState(false);

  const toggleDropdown = useCallback(() => {
    setOpen((visible) => !visible);
  }, []);

  const closeDropdown = useCallback(() => {
    setOpen(false);
  }, []);

  const dropdownRef = useOutsideClick<HTMLDivElement>(closeDropdown);

  return (
    <div className="dropdown relative h-min" ref={dropdownRef}>
      {React.cloneElement(trigger, {
        onClick: toggleDropdown,
      })}
      <AnimatePresence mode="popLayout">
        {open && (
          <motion.ul
            layout
            id="dropdownNavbar"
            className="relative md:absolute bg-black text-sm text-gray-700 dark:text-gray-400"
            initial={{ scaleY: 0, opacity: 0 }}
            animate={{ scaleY: 1, opacity: 1 }}
            exit={{ scaleY: 0, opacity: 0 }}
            style={{ originY: 0 }}
            aria-labelledby="dropdownLargeButton"
          >
            {menu.map((menuItem, index) => (
              <li key={index} className="menu-item">
                {React.cloneElement(menuItem, {
                  onClick: () => {
                    if (menuItem.props.onClick) menuItem.props.onClick();
                    setOpen(false);
                  },
                })}
              </li>
            ))}
          </motion.ul>
        )}
      </AnimatePresence>
    </div>
  );
};

export { Dropdown };
