import { AnimatePresence, motion } from 'framer-motion';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React, { useState } from 'react';

interface AnimatedImagesProps {
  images: IGatsbyImageData[],
  initial?: number
}

const inc = (arr: unknown[], currentIndex: number) => (
  currentIndex === arr.length - 1 ? 0 : currentIndex + 1
);

const AnimatedImages = (props: AnimatedImagesProps) => {
  const { images, initial } = props;
  const [currentImageIndex, setCurrentImageIndex] = useState(initial || 0);

  return (
    <AnimatePresence mode="sync">
      <motion.div
        className="h-full w-full absolute"
        initial={{ opacity: 0 }}
        animate={{ opacity: [0, 0.90, 1], transition: { duration: 8, times: [0, 0.25, 1] } }}
        exit={{ opacity: 0 }}
        transition={{ duration: 2 }}
        key={currentImageIndex}
        onAnimationComplete={() => {
          setCurrentImageIndex(inc(images, currentImageIndex));
        }}
      >
        <motion.div
          className="h-full w-full"
          initial={{ scale: 1 }}
          animate={{ scale: 1.1 }}
          transition={{ duration: 10, ease: 'linear' }}
        >
          <GatsbyImage
            className="h-full w-full brightness-50 overflow-hidden"
            image={images[currentImageIndex]}
            alt="test"
          />
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
};

export { AnimatedImages };
