/* eslint-disable no-undef */
import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { getImage, IGatsbyImageData } from 'gatsby-plugin-image';
import { motion } from 'framer-motion';
import { Section } from 'components/atoms';
import { AnimatedImages } from '../animated-images/animated-images';

const isImage = (item: IGatsbyImageData | undefined): item is IGatsbyImageData => !!item;

const HomeSlidesImages = graphql`
  query HomeSlidesImages {
    allFile(filter: { relativeDirectory: { eq: "home-slides" } }) {
      edges {
        node {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`;

const SectionHome = () => {
  const data = useStaticQuery<Queries.HomeSlidesImagesQuery>(HomeSlidesImages);
  const images: IGatsbyImageData[] = data.allFile.edges
    .map((edge) => getImage(edge.node.childImageSharp?.gatsbyImageData || null))
    .filter(isImage);

  return (
    <Section className="text-white h-[calc(100vh-var(--header-height-sm))] md:h-[calc(100vh-var(--header-height-md))]">
      <div className="h-screen flex flex-col justify-center items-center">
        <motion.h1
          className="text-7xl font-medium uppercase"
          initial={{
            translateY: '100px',
            opacity: 0,
          }}
          animate={{
            scale: 1,
            opacity: 1,
            translateY: 0,
            transition: {
              duration: 0.5,
              type: 'spring',
            },
          }}
        >
          Title
        </motion.h1>
        <motion.h2
          className="text-xl font-light"
          initial={{
            translateY: '100px',
            opacity: 0,
          }}
          animate={{
            scale: 1,
            opacity: 1,
            translateY: 0,
            transition: {
              delay: 0.25,
              duration: 0.5,
              type: 'spring',
            },
          }}
        >
          Subtitle
        </motion.h2>
        <div className="absolute top-0 left-0 z-[-1] h-full w-full bg-black  overflow-hidden">
          { images && <AnimatedImages images={images} /> }
        </div>
      </div>
    </Section>
  );
};

export { SectionHome };
